<template>
  <div class="box" :class="animateClass">
    <Head title="电影"></Head>
    <div class="m-box">
      <p class="m-city" @click="gotoCity">{{city.name}}</p>
      <Carousel id="one" :swiperoptions="swiperOptions" v-if="false">
        <CarouselItem v-for="(item,i) in imgs" :key="i">
          <img :src="item" alt="err" class="m-img" />
        </CarouselItem>
      </Carousel>

      <Carousel id="banner" :swiperoptions="swiperOptions">
        <CarouselItem v-for="(m,i) in obj.banner" :key="i">
          <img :src="m.images.large.replace(/img7/,'img3')" alt="err" class="m-img" />
        </CarouselItem>
      </Carousel>
    </div>

    <div v-if="false">
      <van-button type="default">默认按钮</van-button>
      <van-button type="primary">主要按钮</van-button>
      <van-button type="info">信息按钮</van-button>
      <van-button type="warning">警告按钮</van-button>
      <van-button type="danger">危险按钮</van-button>
      <van-icon name="chat-o" badge="99+" />
    </div>
  </div>
</template>
<script>
import { myMixin, animate } from "@/utils/index.js";
import { mapState, mapActions } from "vuex";

export default {
  mixins: [myMixin, animate],
  data() {
    return {
      imgs: [
        require("@/assets/images/img1.jpg"),
        require("@/assets/images/img2.jpg"),
        require("@/assets/images/img3.jpg"),
        require("@/assets/images/img4.jpg")
      ],
      mv: [],
      swiperOptions: {
        //可能是因为SwiperOptions配置有误，存在轮播图不能滑动问题
        loop: true,
        autoplay: true,
        speed: 1000,
        pagination: {
          // 分页器
          el: ".swiper-pagination",
          clickable: true
        },
        observer: true //修改swiper自己（mv）或子元素时，自动初始化swiper
        // observeParents: false //修改swiper的父元素时，自动初始化swiper
      },
      city: {}
    };
  },
  methods: {
    gotoCity() {
      this.$router.push({ name: "city" });
    },
    ...mapActions([
      "getBanner" // this.getBanner  = this.$store.dispatch('getBanner')
    ])
  },
  computed: {
    ...mapState(["obj"])
  },
  created() {
    setTimeout(() => {
      if (!this.obj.banner.length > 0) {
        this.getBanner(); // vuex 的 临时缓存
      }

      // this.$axios.get("/vue/movie?limit=6").then(res => {
      // this.$nextTick(() => {
      //   this.mv = res.data.result; // 一定会进入 updated
      //   // this.mv.splice(2, 1);
      // });
      // });
    }, 1);
  },
  mounted() {
    if (localStorage.city) {
      let city = JSON.parse(localStorage.city);
      this.city = city;
      console.log(city);
    } else {
      this.$router.push({ name: "city" });
    }
  }
};
</script>

<style lang="scss">
.m-img {
  width: 100%;
  height: 200px;
}
.m-box {
  position: relative;
}
.m-city {
  position: absolute;
  top: 20px;
  left: 20px;
  border: 2px solid #0f0;
  color: #e21340;
  width: 48px;
  height: 20px;
  border-radius: 10px;
  z-index: 10;
  text-align: center;
  line-height: 20px;
  padding: 0 5px;
}
</style>